import * as axios from "axios";
import {bedug_mode} from "../../redux/store-redux";

const instance = axios.create({
  baseURL: 'https://social-network.samuraijs.com/api/1.0/',
  withCredentials: true,
  headers: {
    "API-KEY": "672204d9-92d7-4a15-9913-d64f4d26dd62"
  }
});


export let apiUsers = { //    api  USERS  follow/unfollow
  getUsers: async (currentPage, pageSize, term, friend = undefined) => {//   
    if (bedug_mode) {console.log("getUsers")}
    const response = await instance.get(`users?count=${pageSize}&page=${currentPage}&term=${term}&friend=${friend}`)
    return (response.data) //    data
  },
  postFollow: async (userId) => {//    
    const response = await instance.post(`follow/${userId}`)
    return (response.data) //    data
  },
  deleteFollow: async (userId) => {//    
    const response = await instance.delete(`follow/${userId}`)
    return (response.data) //    data
  }
}

export let apiProfile = { //    api    
  getProfile: async (userId) => {//       userId
    const response = await instance.get(`profile/` + userId)
    return (response.data) //    data
  },
  getAuthMe: async () => {//  " ?"
    const response = await instance.get(`auth/me`)
    return (response.data) //    data
  },
  getStatus: async (userId) => { //      userId
    const response = await instance.get(`/profile/status/${userId}`)
    return (response.data) //    data
  },
  putStatus: async (statusTmpInput) => { //   
    const response = await instance.put(`/profile/status/`, {status: statusTmpInput})
    return (response.data) //    data
  },
  postLogin: async (email, password, rememberme) => { //       login 
    const response = await instance.post(`/auth/login`, {email: email, password: password, rememberme: rememberme})
    return (response.data) //    data
  },
  deleteLogin: async () => { //   
    const response = await instance.delete(`/auth/login`)
    return (response.data) //    data
  }
}

export let apiDialogs2 = { //    api  Dialogs
  getFollow: async (dialogUserID) => {//  follow/unfollow  
    const response = await instance.get(`follow/${dialogUserID}`)
    return (response.data) //    data
  }
}






