import {applyMiddleware, combineReducers, createStore} from "redux";
import thunkMiddleWare from 'redux-thunk'; // thunkMiddleWare        
import profileReducer from "./profile-reducer";
import dialogsReducer from "./dialogs-reducer";
import sidebarReducer from "./sidebar-reducer";
import usersReducer from "./users-reducer";
import authReducer from "./auth-reducer";
import { reducer as formReducer } from 'redux-form'
import appReducer from "./app-reducer";
import darkLightReducer from "./dark-light-reducer";

let reducers = combineReducers({ //       store
    profilePage: profileReducer, //  
    dialogsPage: dialogsReducer, //  
    sideBar: sidebarReducer, //  
    usersPage: usersReducer, //   
    auth: authReducer, //   
    form: formReducer, //   redux-form
    app: appReducer, //   
    theme: darkLightReducer //   (dark-light)
});

let store = createStore(reducers, applyMiddleware(thunkMiddleWare));//ApplyMiddleWare     UI  ,       ,   .
window.store = store; //     

export let bedug_mode = store.getState().app.bedug_mode
export let debugItem = "=>"

export default store
