import React from "react";
import "./krestiki-noliki.css";

const Square = (props) => {
    //   ( )
    return (
        <button
            className="square" //  /
            onClick={() => {
                //  
                props.handleSquare(props.i); //     
            }}
        >
            {props.value} {/*    X, O   (null)*/}
        </button>
    );
};

class Board extends React.Component {
    //     9 
    renderSquare(i) {
        //      Square
        return (
            <Square //  Square  
                value={this.props.squares[i]} //  X, O   (null)
                handleSquare={this.props.handleSquare} //    ,    
                i={i} //   
            />
        );
    }

    render() {
        let status; //   status
        const Winner = calculateWinner(this.props.squares); // ,   
        if (this.props.history.length < 10) {
            status = this.props.xIsNext ? " : X " : " : O ";
            //      X  O
        }
        if (Winner) {//  
            status = ": " + Winner; //  
        }
        if (this.props.history.length >= 10 && !Winner) {
            status = ""; //      
        }
        return (
            <div>
                <div className="status">{status}</div>
                {/*   */}
                <div className="board-row">
                    {this.renderSquare(0)} {/* 3     i*/}
                    {this.renderSquare(1)}
                    {this.renderSquare(2)}
                </div>
                <div className="board-row">
                    {/* ,   3 */}
                    {this.renderSquare(3)}
                    {this.renderSquare(4)}
                    {this.renderSquare(5)}
                </div>
                <div className="board-row">
                    {/* ,   3 */}
                    {this.renderSquare(6)}
                    {this.renderSquare(7)}
                    {this.renderSquare(8)}
                </div>
            </div>
        );
    }
}

function calculateWinner(squares) {
    //    (squares -    )
    const lines = [
        [0, 1, 2], //   a, b, c    
        [3, 4, 5],
        [6, 7, 8],
        [0, 3, 6],
        [1, 4, 7],
        [2, 5, 8],
        [0, 4, 8],
        [2, 4, 6]
    ];
    for (let n = 0; n < lines.length; n = n + 1) {
        //   squares
        const [a, b, c] = lines[n]; //  a, b,   
        //   
        if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
            //         
            return squares[a]; //    (X  O)  
        }
    }
    return null; //     ,   
}

class KrestikiNoliki extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            xIsNext: true, //   X  O (true/false)
            history: [{squaresNew: Array(9).fill(null)}] //   -  
        };
    }

    handleSquare = (i) => {
        //  
        let currentStep = this.state.history.length - 1; //      (-1     0)
        let squaresNew = this.state.history[currentStep].squaresNew.slice(); //    

        const Winner = calculateWinner(squaresNew); //   

        if (squaresNew[i] || Winner) {
            //     ,    
            return; //   
        }
        squaresNew[i] = this.state.xIsNext ? "X" : "O";
        //      X  O    xIsNext
        this.setState({xIsNext: !this.state.xIsNext});
        //    

        const historyNew2 = this.state.history.concat({squaresNew});
        //     historyNew2     history    squaresNew

        this.setState({history: historyNew2}); //   history   historyNew2
    };

    newGame = () => {
        this.setState({
            xIsNext: true,
            history: [{squaresNew: Array(9).fill(null)}]
        });

    }

    render() {
        let currentStep = this.state.history.length - 1; //      (-1     0)
        let squaresNew = this.state.history[currentStep].squaresNew.slice(); //    
        return (
            <div>
                <div><h3> </h3></div>
                <div>
                    <Board
                        squares={squaresNew} //      
                        xIsNext={this.state.xIsNext} //   
                        handleSquare={this.handleSquare} //  
                        history={this.state.history}
                    />
                </div>
                <div>
                    <button className="new-game-button" onClick={this.newGame}> </button>
                </div>
            </div>
        );
    }
}

export default KrestikiNoliki;


