// async  await   
import axios from "axios"; //   
import React from "react";
import ScrollContainer from "../common/Scroll/ScrollContainer"; //      JSX

class News extends React.Component {
    state = {
        //  
        data: { hits: [] }, //   ,     
        query: "react", //  
        onChangeQuery: "react" //   ,   onChange
    };

    inputOnChange = (event) => {
        //
        this.setState({ onChangeQuery: event.currentTarget.value });
        //      onChange input
    };

    buttonOnClick = () => {
        this.setState({ query: this.state.onChangeQuery }); //   
        //    query    onChangeQuery
    };

    callURL = (props) => {
        //      URL 
        return `https://hn.algolia.com/api/v1/search?query=${props}`;
    };

    async AsyncF(props) {
        //  
        const result = await axios(this.callURL(props));
        this.setState({ data: result.data }); //    hits   
    }

    componentDidMount() {
        this.AsyncF(this.state.query);
        //        query
    }

    componentDidUpdate(prevProps, prevState) {
        //  
        if (this.state.query !== prevState.query) {
            //   query 
            this.AsyncF(this.state.query); //    
        }
    }

    render() {
        let hn = <div>
            <h3>Search by HackerNews</h3>
            <form>
                {/*  input  button*/}
                <input //  
                    value={this.state.onChangeQuery} //  value    
                    onChange={(event) => {
                        //  
                        this.inputOnChange(event); //      onChange input
                    }}
                />
                <button // 
                    onClick={(e) => {
                        //  
                        e.preventDefault(); //     ( )
                        this.buttonOnClick();
                        //    query    onChangeQuery
                    }}
                >
                    Search
                </button>
            </form>

            <ul>
                {this.state.data.hits ? ( //   hits  
                    <div>
                        {this.state.data.hits.map((
                            //
                            d
                        ) => (
                            <li key={d.objectID}>
                                {" "}
                                {/* key   map id*/}
                                <a href={d.url} >
                                    {d.title}
                                    {d.story_title}
                                </a>
                                {/*    URL*/}
                            </li>
                        ))}
                    </div>
                ) : null}{" "}
                {/*   ,   hits */}
            </ul>
        </div>
        return (    <div>
                <ScrollContainer
                    child={hn}
                    height={window.screen.availHeight-280}
                    firstInsideContainer={"UsersUp"}
                    secondInsideContainer={"UsersDown"}
                    containerElement={"UserContainer"}
                /> {/* Users   */}


            </div>
        );
    }
}
export default News;
