import React, {Suspense} from "react";
import classes from "../Content/ContentContainer.module.css";
import {Route, Routes} from "react-router-dom";
import DialogsInfo from "./DialogsInfo";
import LoginInfo from "./LoginInfo";
import ProfileInfo2 from "../Profile/ProfileInfo/ProfileInfo2";
import NewsInfo from "./NewsInfo";
import RestInfo from "./RestInfo";
import UsersInfo from "./UsersInfo";
import IndexInfo from "./IndexInfo";
import ScrollContainer from "../common/Scroll/ScrollContainer";




let InfoContainer = () => {
    let info = <div className={classes.contentClass}>
        <Routes> {/*   URL   */}
            <Route path='/profile/*' element={<ProfileInfo2/>}/>{/* */}
            <Route path='/dialogs/*' element={<DialogsInfo/>}/> {/* */}
            <Route path='/users/*' element={<UsersInfo/>}/> {/*   */}
            <Route path='/login/*' element={<LoginInfo/>}/> {/* */}
            <Route path='/news/*' element={<NewsInfo/>}/>{/* */}
            <Route path='/rest/*' element={<RestInfo/>}/> {/* */}
            <Route path='' element={<IndexInfo/>}/> {/* */}
        </Routes>
    </div>

    return (<div>
        <Suspense fallback={
            <div>...</div>}> {/* ,    Lazy import   fallback   */}
        <ScrollContainer //   
            child={info}
            height={window.screen.availHeight - 270} //   
            firstInsideContainer={"DialogsUp"}
            secondInsideContainer={"DialogsDown"}
            containerElement={"DialogsContainer"}
        /> {/*    */}
        </Suspense>
    </div>)

}
export default InfoContainer
