import {apiProfile} from "../components/api/api";
import {stopSubmit} from "redux-form";
import {bedug_mode} from "./store-redux";
import {friendsInitialState, setFriends} from "./sidebar-reducer";
import {appInitialState} from "./app-reducer";
import {dialogsInitialState} from "./dialogs-reducer";
import {profileInitialState} from "./profile-reducer";
import {usersInitialState} from "./users-reducer";

const SET_MY_DATA = "myApp/auth-reducer/SET_MY_DATA"; //        (ID, Email, login, isAuth)
const SET_MY_PROFILE = "myApp/auth-reducer/SET_MY_PROFILE"; //      
const AUTH_INITIAL_STATE = "myApp/auth-reducer/AUTH_INITIAL_STATE"; //   

export let setAuthData = (id, email, login, isAuth) => { //     ID, Email, login
    return {type: SET_MY_DATA, id, email, login, isAuth}
};
export let setMyProfile = (myProfile) => { //       
    return {type: SET_MY_PROFILE, myProfile}
};
export let authInitialState = () => { //     
    return {type: AUTH_INITIAL_STATE}
};

let initialState = { //      
    myID: null, //  ID  
    myEmail: null,//  Email  
    myLogin: null,//    
    isAuth: false, //  
    myProfile: null //     
}

let authReducer = (state = initialState, action) => { //     
    let stateCopy; //      
    switch (action.type) {
        case SET_MY_DATA: //    id, email, login
            stateCopy = {
                ...state,
                myID: action.id,
                myEmail: action.email,
                myLogin: action.login,
                isAuth: action.isAuth,
            }
            if (bedug_mode) {console.log("auth-reducer.jsx, SET_MY_DATA: ", state, stateCopy)} // 

            return stateCopy; //     
        case SET_MY_PROFILE: //     
            stateCopy = {
                ...state,
                myProfile: action.myProfile
            }
            if (bedug_mode) {console.log("auth-reducer.jsx, SET_MY_PROFILE: ", state, stateCopy)} // 
            return stateCopy; //     
        case AUTH_INITIAL_STATE: //    
            stateCopy = initialState
            if (bedug_mode) {console.log("auth-reducer.jsx, AUTH_INITIAL_STATE: ", state, stateCopy)} // 
            return stateCopy; //     
        default:
            return state; //     
    }
}

export let getAuthMeThunkCreator = () => {//  ?.    
    let getAuthMeThunk = async (dispatch) => {
        const response1 = await apiProfile.getAuthMe() //  ?
        if (response1.resultCode === 0) { //  
            let id = response1.data.id; //     ID
            let email = response1.data.email; //     
            let login = response1.data.login; //     
            let isAuth = true; //    
            if (bedug_mode) {console.log("auth-reducer.jsx, getAuthMeThunkCreator.await getAuthMe() : dispatch(setAuthData)->SET_MY_DATA  ( )" )} // 
            dispatch(setAuthData(id, email, login, isAuth))//    
            const response2 = await apiProfile.getProfile(id)//     
            if (bedug_mode) {console.log("auth-reducer.jsx, getAuthMeThunkCreator.await(getAuthMe)->await .getProfile() : dispatch(setMyProfile()->SET_MY_PROFILE" )} // 
            dispatch(setMyProfile(response2))//     
        }
        if (!response1.resultCode == 0) { //  
            let id = initialState.userID; //     ID
            let email = initialState.email;//     email
            let login = initialState.login;//     
            let isAuth = false; //   
            if (bedug_mode) {console.log("auth-reducer.jsx, getAuthMeThunkCreator.await getAuthMe() : dispatch(setAuthData)->SET_MY_DATA (  )" )} // 
            dispatch(setAuthData(id, email, login, isAuth))//        
        }
    }
    return getAuthMeThunk;
}
export let postLoginThunkCreator = (email, password, rememberme) => {//  
    let postLoginThunk = async (dispatch) => { //    
        const response = await apiProfile.postLogin(email, password, rememberme) //       
        if (response.resultCode === 0) { //     
            if (bedug_mode) {console.log("auth-reducer.jsx, postLoginThunkCreator.await .postLogin(): dispatch(getAuthMeThunkCreator())" )} // 
            dispatch(getAuthMeThunkCreator()) //      
        } else { //      
            let message =  //    message -   
                !response.messages[0] //     
                    ? "no responce from server" //   
                    : response.messages[0] //     
            let action = stopSubmit("loginForm", {_error: message})
            // loginForm    .
            //  _error         message
            if (bedug_mode) {console.log("auth-reducer.jsx, postLoginThunkCreator.await /      : dispatch(action) //    " )} // 
            dispatch(action) //    
        }
    }
    return postLoginThunk;
}
export let deleteLoginThunkCreator = () => {//  
    let deleteLoginThunk = async (dispatch) => { //    
        const response = await apiProfile.deleteLogin() //    
        if (response.resultCode === 0) { //    
            setTimeout(()=>{

                if (bedug_mode) {console.log("auth-reducer.jsx, deleteLoginThunkCreator.await .deleteLogin():dispatch(dialogsInitialState())->DIALOGS_INITIAL_STATE" )} // 
                dispatch(dialogsInitialState())//    

                if (bedug_mode) {console.log("auth-reducer.jsx, deleteLoginThunkCreator.await .deleteLogin():dispatch(authInitialState())->AUTH_INITIAL_STATE" )} // 
                dispatch(authInitialState())//    

                if (bedug_mode) {console.log("auth-reducer.jsx, deleteLoginThunkCreator.await .deleteLogin():dispatch(profileInitialState())->PROFILE_INITIAL_STATE" )} // 
                dispatch(profileInitialState())//    


                if (bedug_mode) {console.log("auth-reducer.jsx, deleteLoginThunkCreator.await .deleteLogin():dispatch(friendsInitialState())->FRIENDS_INITIAL_STATE" )} // 
                dispatch(friendsInitialState())//  FriendList  

                if (bedug_mode) {console.log("auth-reducer.jsx, deleteLoginThunkCreator.await .deleteLogin():dispatch(usersInitialState())->USERS_INITIAL_STATE" )} // 
                dispatch(usersInitialState())//  Users  

            },300)
           // sdfsadfsaf



        } else {//    
        }
    }
    return deleteLoginThunk;
}

export default authReducer;










